import EyeAndOverlayLayer from './EyeAndOverlayLayer';
import Conversion from '../utils/Conversion';

let EyeInfo = EyeAndOverlayLayer.extend({
    layerType: "Eye",
    dofs: {
        eyeSubRootBn_r: true,
        eyeSubRootBn_t: true,
        eyeSubRootBn_t_2: true,
        vertexJoint1_t: true,
        vertexJoint1_t_2: true,
        vertexJoint2_t: true,
        vertexJoint2_t_2: true,
        vertexJoint3_t: true,
        vertexJoint3_t_2: true,
        vertexJoint4_t: true,
        vertexJoint4_t_2: true,
        vertexJoint5_t: true,
        vertexJoint5_t_2: true,
        vertexJoint6_t: true,
        vertexJoint6_t_2: true,
        vertexJoint7_t: true,
        vertexJoint7_t_2: true,
        vertexJoint8_t: true,
        vertexJoint8_t_2: true,
        vertexJoint9_t: true,
        eyeVisibilityBn_r: true,
        eye_alphaChannelBn_r: true
    }
});

export default class EyeLayer extends EyeAndOverlayLayer {

    static getInfo() {
        return EyeInfo;
    }

    static generateDofs(props) {
        let dofs = EyeAndOverlayLayer._generateDofs(props, "");
        dofs.eyeSubRootBn_r = props.Rotate/180 * Math.PI;
        dofs.eyeSubRootBn_t = Conversion.toMetersX(props.Translate.x);
        dofs.eyeSubRootBn_t_2 = Conversion.toMetersY(props.Translate.y);
        dofs.eyeVisibilityBn_r = props.Visible === undefined ? 1 : (props.Visible ? 1 : 0);
        dofs.eye_alphaChannelBn_r = props.Alpha === undefined ? 1 : props.Alpha;
        return dofs;
    }
} 